import {
  AfterContentInit,
  Component,
  Input,
  OnInit
} from '@angular/core';
import { ConfigService } from '../../config.service';
import { VisualizationComponent } from '../visualization/visualization.component';

@Component({
  selector: 'lib-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit, AfterContentInit {
  @Input() visual: VisualizationComponent = {} as any;

  constructor(
    public configService: ConfigService
  ) { }

  uiElementsListing(): string[] {
    return this.configService.config.uiElements;
  }

  ngOnInit(): void { }

  ngAfterContentInit(): void {
    setTimeout(() => {
      const elements = this.configService.config.uiElements;
      this.appendElements(elements);
    }, 10);
  }

  appendElements(elements: string[]): void {
    elements.forEach((elementName: string) => {
      const elementContainer = document.querySelector('#' + elementName) as HTMLElement;
      const elementHtml = document.querySelector(elementName) as HTMLElement;
      if (elementContainer && elementHtml) {
        elementContainer.appendChild(elementHtml);
      }
    });
  }
}
