import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { ConfigService } from './config.service';
import { SnowplowService } from './snowplow/snowplow.service';

@Component({
  selector: 'lib-data-viz-ui',
  templateUrl: './data-viz-ui.component.html',
  styleUrls: [],
})
export class DataVizUiComponent implements OnInit, OnChanges {

  @Input() modules: object = {
    header: true,
    bookmarks: true,
  };
  @Input() bookmarks: object = {
  };
  @Input() uiElements: string[] = [];
  @Input() roles: string[] = [];

  constructor(
    public configService: ConfigService,
    private snowplowService: SnowplowService) {
  }

  ngOnInit(): void {
    // Set userId in Snowplow
    this.snowplowService.setUserId();
    // Set appSessionId
    this.snowplowService.setAppSessionId();

  }

  ngOnChanges(changes: SimpleChanges): void {
    Object.keys(changes).forEach(key => {
      changes[key] = changes[key].currentValue;
    });
    this.configService.updateValues(changes);
  }
}
