export default {
  sort(array: any[], orderType?: string, ascending?: boolean, key?: string): any[] {
    return this.sortAlphabetically(array, ascending, key);
  },

  sortAlphabetically(array: any[], ascending?: boolean, key?: string): any[] {
    array.sort((a, b) => {
      const la = a.bookmarkName.toLowerCase();
      const lb = b.bookmarkName.toLowerCase();
      if (la > lb) {
        return 1;
      }
      if (la < lb) {
        return -1;
      }
      return 0;
    });
    return ascending ? array : array.reverse();
  },
};
