import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map, catchError } from 'rxjs/operators';
import { Observable, throwError } from 'rxjs';
import { Config } from './../../authentication/config';
import { AuthenticationService } from './../../authentication/authentication.service';
import { ConfigService } from '../../config.service';
import { PowerbiBookmark } from '../visualization/visualization.model';
import { DataVizUiEnvironmentManager } from '../../config/data-viz-ui-environment-manager';

@Injectable({ providedIn: 'root' })
export class BookmarkService {
  private apiUrl = this.apiConfig.getConfig().apiUrl;
  private reportName: string;
  private tenantId: string;
  constructor(
    private http: HttpClient,
    private apiConfig: Config,
    private configService: ConfigService,
    private authService: AuthenticationService
  ) {
    this.reportName = configService.config['roles'].
      includes('Manufacturer') ? 'MAN' : 'DIST';
    this.tenantId = DataVizUiEnvironmentManager.getTenant();
  }

  getBookmarks(reportName?: string): Observable<any[]> {
    if (reportName) {
      this.reportName = reportName;
    }
    return this.http
      .get<any>(this.apiUrl + `api/bookmarks/v1/api/bookmarks?reportName=${this.reportName}&tenantId=${this.tenantId}`)
      .pipe(
        map((bookmarks) => {
          return bookmarks;
        }),
        catchError((error) => throwError(error))
      );
  }
  createBookmark(bookmark: PowerbiBookmark): Observable<any> {
    const requestData = {} as any;
    requestData.bookmarkName = bookmark.bookmarkName;
    requestData.bookmarkState = bookmark.state;
    requestData.reportName = this.reportName;
    requestData.tenantId = this.tenantId;
    return this.http
      .post(this.apiUrl + 'api/bookmarks/v1/api/bookmarks', requestData)
      .pipe(
        map((data) => {
          catchError(this.handleError);
          return data;
        })
      );
  }
  updateBookmark(editedBookmark: PowerbiBookmark): Observable<any> {
    const requestData = {} as any;
    requestData.bookmarkName = editedBookmark.bookmarkName;
    requestData.bookmarkState = editedBookmark.bookmarkState;
    requestData.reportName = this.reportName;
    requestData.tenantId = this.tenantId;
    return this.http
      .put(this.apiUrl + 'api/bookmarks/v1/api/bookmarks/' + editedBookmark.bookmarkId, requestData)
      .pipe(
        map((data) => {
          catchError(this.handleError);
          return data;
        })
      );
  }
  updateBookmarkName(editedBookmark: PowerbiBookmark, newName: string): Observable<any> {
    const requestData = {} as any;
    requestData.bookmarkName = newName;
    requestData.bookmarkState = editedBookmark.bookmarkState;
    requestData.reportName = this.reportName;
    return this.http
      .put(
        this.apiUrl +
        `api/bookmarks/v1/api/bookmarks/${editedBookmark.bookmarkId}`,
        requestData
      )
      .pipe(
        map((data) => {
          catchError(this.handleError);
          return data;
        })
      );
  }
  deleteBookmark(bookmark: PowerbiBookmark): Observable<any> {
    return this.http
      .delete(
        this.apiUrl + `api/bookmarks/v1/api/bookmarks/${bookmark.bookmarkId}`,
        { responseType: 'text' }
      )
      .pipe(
        map((data) => {
          // catchError(this.handleError);
          return data;
        })
      );
  }

  handleError(error: object): Observable<never> {
    console.log(error);
    let errorMessage = '';
    if ((error as any).error instanceof ErrorEvent) {
      // client-side error
      errorMessage = `Error: ${(error as any).error.message}`;
    } else {
      // server-side error
      errorMessage = `Error Code: ${(error as any).status}\nMessage: ${(error as any).message}`;
    }
    window.alert(errorMessage);
    return throwError(errorMessage);
  }
}
