
export class AnalyticsStructuredEventsMapping {

  public static STRUCTURED_ACTION = {
    click: 'Click',
    load: 'Page Load'
  };

  // Structured Events
  public static STRUCTURED_CATEGORY = {
    'load-dashboard': 'hida-load-dashboard',
    login: 'hida-login',
    logout: 'hida-logout',
    'create-bookmark': 'hida-create-bookmark',
    'update-bookmark': 'hida-update-bookmark',
    'delete-bookmark': 'hida-delete-bookmark',
    'load-bookmarks': 'hida-load-bookmarks',
    'select-bookmarks': 'hida-select-bookmarks',
    'page-changed': 'hida-page-changed'
  };

  public static fetchCategory(category: string): string {
    const categories: { [key: string]: string } = this.STRUCTURED_CATEGORY;
    return categories[category] || category;
  }

  public static fetchAction(action: string): string {
    const actions: { [key: string]: string } = this.STRUCTURED_ACTION;
    return actions[action] || action;
  }
}
