import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { Config } from './interfaces/config.interface';

@Injectable({
  providedIn: 'root'
})
export class ConfigService {

  configObject$ = new Subject<any>();

  config: Config = {
    modules: {
      header: true,
      bookmarks: true,
    },
    bookmarks: {
      envBasedReportIdsMap: {
        dev: {
          default: '',
        },
        qa: {
          default: '',
        },
        staging: {
          default: '',
        },
        prod: {
          default: '',
        }
      },
      embedUrlQueryParamsMap: {
        default: {
        }
      }
    },
    uiElements: [],
    roles: [],
  };

  constructor() { }

  getConfigObject(): Observable<any> {
    return this.configObject$.asObservable();
  }
  updateValues(values: object): void {
    Object.assign(this.config, values);
    this.configObject$.next(this.config);
  }
}
