import { AnalyticsEnvironment, ANALYTICS_ENVIRONMENT } from '@cdx/analytics';
import { AppConfig } from './app_config';

export class AnalyticsConfig {

  public static SNOWPLOW_ENVIRONMENTS_MAPPING: { [key: string]: string } = {
    dev: 'SNAPSHOT',
    qa: 'STABLE',
    prod: 'PROD',
  };
  public static SNOWPLOW_PARAMS = {
    appId: 'DATAVIZ',
    options: {
      snowplowEnvironmentProvider: {
        provide: ANALYTICS_ENVIRONMENT,
        useFactory: getEnvironment,
      },
      tracking: {
        inferred: false,
        pageViews: true
      }
    }
  };
}

export function getEnvironment(): AnalyticsEnvironment {
  const snowplowEnvironment = AnalyticsConfig.SNOWPLOW_ENVIRONMENTS_MAPPING[AppConfig.fetchAppEnv()] || 'SNAPSHOT';
  return snowplowEnvironment as AnalyticsEnvironment;
}
