import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  Router,
  RouterStateSnapshot,
} from '@angular/router';
import { Observable } from 'rxjs';

import { AuthenticationService } from './authentication.service';
import { DataVizUiService } from './../data-viz-ui.service';

@Injectable({
  providedIn: 'root',
})
export class AuthenticationGuard implements CanActivate {
  constructor(
    private authService: AuthenticationService,
    private dataVizUiService: DataVizUiService,
    private router: Router
  ) {}

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean> | Promise<boolean> | boolean {
    return this.checkLogin(state.url);
  }

  checkLogin(url: string): boolean {
    if (this.authService.isAuthenticated()) {
      // const userRole = this.authService.getUserRole();
      // if (userRole !== 'MANUFACTURER' && userRole !== 'DISTRIBUTOR') {
      //   this.router.navigate(['/unauthorized']);
      // }
      this.dataVizUiService.rolesObject$.next(this.authService.getUserRole());
      return true;
    }

    // Store the attempted URL for redirecting
    this.authService.setRedirectUrl(url);

    // Navigate to the login or ping page with extras
    url.includes('ping')
      ? this.router.navigate(['/ping-login'])
      : this.router.navigate(['/login']);
    return false;
  }
}
